.bounding-box {
  position: absolute;
  box-shadow: inset 0 0 0 3px #149df2  /*149df2  82B1E1  928BE5 A463F2*/;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  cursor: pointer;
}
.Tilt {
  background: -webkit-linear-gradient(right,  #CBCFD3 0%, #3A5064 100%);
  background: linear-gradient(270deg,  #CBCFD3 0%, #3A5064 100%); /* w3c */
}
.form {
  width: 700px;
  background:
  -webkit-linear-gradient(315deg, #708090 22px, #d9ecff 22px, #d9ecff 24px, transparent 24px, transparent 67px, #d9ecff 67px, #d9ecff 69px, transparent 69px),
  -webkit-linear-gradient(225deg, #708090 22px, #d9ecff 22px, #d9ecff 24px, transparent 24px, transparent 67px, #d9ecff 67px, #d9ecff 69px, transparent 69px)0 64px;
  background:
  linear-gradient(135deg, #708090 22px, #d9ecff 22px, #d9ecff 24px, transparent 24px, transparent 67px, #d9ecff 67px, #d9ecff 69px, transparent 69px),
  linear-gradient(225deg, #708090 22px, #d9ecff 22px, #d9ecff 24px, transparent 24px, transparent 67px, #d9ecff 67px, #d9ecff 69px, transparent 69px)0 64px;
  background-color:#708090;
  background-size: 64px 128px;
}

#modal-root {
  position: relative;
  z-index: 999;
}

.profile-modal {
  background-color: rgba(255, 255, 255, 0.8);
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-close {
  font-size: 3rem; 
  font-style: bold;
  cursor: pointer;
}

.modal-close:hover {
  color: gray;
  cursor: pointer;
}

.App {
  text-align: center;
}

.center {
  display: flex; 
  justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Courier New", Courier, monospace;
  /* background: linear-gradient(89deg, #363535 0%, #04c8de 100%); w3c */
  background: -webkit-linear-gradient(left, #CBCFD3 0%, #3A5064 100%);
  background: linear-gradient(90deg, #CBCFD3 0%, #3A5064 100%);
}

button {
  cursor: pointer;
}

